@header-height: 50px;
@sidebar-width: 250px;
@sidebar-min-width: 80px;

//color
@primary: #1890ff;
@success: #36b389;
@warning: #f7a500;
@danger: #f55656;
@info: #498df2;

@bg: #f7f7f7;
@border: #e6e6e6;
@border-color: #e6e6e6;

@font: #666;
@font-dark: #333;
@font-light: #999;
@font-lighter: #ccc;
@font-lightest: #e6e6e6;
//border
@border-solid: 1px @border solid;
@border-radius: 4px;

//size
@font-size: 14px;
@h1: 20px;
@h2: 18px;
@h3: 16px;
@h4: @font-size;
@h5: 12px;

//media
@sm: 768px;
@md: 1024px;
@lg: 1360px;

