//清浮动
.clearfix() {
  &:before,
  &:after {
    content: '';
    display: table;
  }

  &:after {
    clear: both;
    overflow: hidden;
  }
}

.wrapper {
  height: @header-height;
  background: #fff;
  color: @font;
  padding-right: 25px;
  width: 100%;
  border-bottom: 1px solid @border;
}

.back {
  float: left;
  line-height: @header-height;
}

.headerLeft {
  float: left;
}
.logo {
  float: left;
  padding: 5px 15px;
  width: 200px;
  > img {
    width: 100%;
  }
}

.title {
  line-height: @header-height;
  border-left: 1px solid @border;
  padding-left: 0.5em;
}

.collapsedBtn {
  height: @header-height;
  width: @header-height;
  border: 0;
  background: transparent;
  line-height: @header-height;
  color: @font;
}

.navRight {
  float: right;
  list-style: none;
  line-height: @header-height;
  > a {
    float: left;
    color: @font;
    line-height: @header-height;
    padding: 0 10px;
  }
}

.project {
  float: left;
  line-height: @header-height;
}
